.nav-links {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
  gap: 1rem;
}

.nav-links li {
  margin: 0;
}

.nav-links a {
  color: #2d3436;
  text-decoration: none;
  padding: 0.5rem 1rem;
  border-radius: 50px;
  font-weight: 500;
  transition: all 0.3s ease;
  border: 1px solid transparent;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.nav-links a:hover,
.nav-links a:active,
.nav-links a.active {
  background: #00b894;
  color: white;
  border-color: #00b894;
  transform: translateY(-2px);
  box-shadow: 0 4px 15px rgba(0, 184, 148, 0.3);
}

.nav-links button {
  cursor: pointer;
  border: 1px solid #00b894;
  color: #2d3436;
  background: transparent;
  padding: 0.5rem 1rem;
  font: inherit;
  border-radius: 50px;
  transition: all 0.3s ease;
}

.nav-links button:focus {
  outline: none;
}

.nav-links button:hover,
.nav-links button:active {
  background: #00b894;
  color: white;
  transform: translateY(-2px);
  box-shadow: 0 4px 15px rgba(0, 184, 148, 0.3);
}

.notification-bell {
  position: relative;
  margin-left: 0.5rem;
}

.notification-button {
  background: none;
  border: none;
  cursor: pointer;
  padding: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #2d3436;
  font-size: 1.2rem;
  transition: all 0.3s ease;
}

.notification-button:hover {
  color: #00b894;
  transform: translateY(-2px);
}

/* Tablet and larger screens */
@media (min-width: 768px) {
  .nav-links {
    flex-direction: row;
    align-items: center;
    padding: 0;
    gap: 1rem;
  }

  .nav-links li {
    width: auto;
  }

  .nav-links a {
    display: flex;
    width: auto;
    text-align: center;
    padding: 0.5rem 1rem;
  }

  .notification-bell {
    margin-left: 0.5rem;
    width: auto;
  }

  .notification-button {
    width: auto;
    justify-content: center;
    padding: 0.5rem;
  }
}

/* Mobile screens */
@media (max-width: 767px) {
  .nav-links {
    flex-direction: column;
    align-items: flex-start;
    padding: 1rem;
    gap: 0.5rem;
  }

  .nav-links li {
    width: 100%;
  }

  .nav-links a {
    display: flex;
    width: 100%;
    text-align: left;
    padding: 0.8rem 1rem;
    font-size: 1.1rem;
  }

  .notification-bell {
    margin-left: 0;
    width: 100%;
  }

  .notification-button {
    width: 100%;
    justify-content: flex-start;
    padding: 0.8rem 1rem;
    font-size: 1.1rem;
  }
}

/* Small mobile screens */
@media (max-width: 480px) {
  .nav-links {
    padding: 0.75rem;
    gap: 0.25rem;
  }

  .nav-links a {
    padding: 0.6rem 0.75rem;
    font-size: 1rem;
  }

  .notification-button {
    padding: 0.6rem 0.75rem;
    font-size: 1rem;
  }
}
