.scroll-arrow {
  position: absolute;
  bottom: 2rem;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;
  color: #2d3436;
  opacity: 0;
  animation: fadeIn 1s ease forwards 1s;
  cursor: pointer;
  z-index: 10;
}

.scroll-arrow__text {
  font-size: 0.9rem;
  font-weight: 500;
}

.scroll-arrow__icon {
  font-size: 1.5rem;
  animation: bounce 2s infinite;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translate(-50%, 20px);
  }
  to {
    opacity: 1;
    transform: translate(-50%, 0);
  }
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-10px);
  }
  60% {
    transform: translateY(-5px);
  }
}

@media (max-width: 768px) {
  .scroll-arrow {
    bottom: 1.5rem;
  }
} 