.modal {
  z-index: 100;
  position: fixed;
  top: 15%;
  left: 25%;
  transform: translate(-50%, -50%);
  width: 50%;
  max-width: 800px;
  max-height: 80vh;
  background: white;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  border-radius: 16px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.modal__header {
  width: 100%;
  padding: 1.5rem;
  background: white;
  color: #2d3436;
  border-bottom: 1px solid #dfe6e9;
  flex-shrink: 0;
}

.modal__header h2 {
  margin: 0;
  font-size: 1.5rem;
  font-weight: 600;
}

.modal__content {
  padding: 1.5rem;
  overflow-y: auto;
  flex-grow: 1;
  min-height: 0;
}

.modal__footer {
  padding-bottom: 1rem;
  background: white;
  display: flex;
  justify-content: center;
  margin-right: 6rem;
  gap: 1rem;
  flex-shrink: 0;
}

.modal__footer button {
  min-width: 120px;
  padding: 0.8rem 1.2rem;
  font-size: 0.95rem;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
}

@media (max-width: 768px) {
  .modal {
    width: 90%;
    left: 50%;
    top: 50%;
  }

  .modal__header {
    padding: 1rem;
  }

  .modal__content {
    padding: 1rem;
  }

  .modal__footer {
    padding: 1rem;
    flex-direction: column;
  }

  .modal__footer button {
    width: 100%;
  }
}

.modal-enter {
  transform: translateY(-10rem);
  opacity: 0;
}

.modal-enter-active {
  transform: translateY(0);
  opacity: 1;
  transition: all 200ms;
}

.modal-exit {
  transform: translateY(0);
  opacity: 1;
}

.modal-exit-active {
  transform: translateY(-10rem);
  opacity: 0;
  transition: all 200ms;
}
