.button {
  font: inherit;
  padding: 0.5rem 3rem;
  border: 1px solid #ffd700;
  border-radius: 4px;
  background: #ffd700;
  color: #010101;
  cursor: pointer;
  margin-left: 6rem;
  margin-top: 4rem;
  text-decoration: none;
  display: inline-block;
}

.button:focus {
  outline: none;
}

.button:hover {
  background: #010101;
  border-color: #696969;
  color: #ffd700;
  transform: translateY(-4px) translateX(-2px);
  box-shadow: 2px 5px 0 0 black;
}

.button:active {
  background: #010101;
  border-color: #696969;
  color: #ffd700;
  transform: translateY(2px) translateX(1px);
  box-shadow: 0 0 0 0 black;
}

.button--inverse {
  background: transparent;
  color: #ff0055;
}

.button--inverse:hover,
.button--inverse:active {
  color: white;
  background: #ff0055;
}

.button--danger {
  background: #830000;
  border-color: #830000;
}

.button--danger:hover,
.button--danger:active {
  background: #f34343;
  border-color: #f34343;
}

.button:disabled,
.button:hover:disabled,
.button:active:disabled {
  background: #ccc;
  color: #979797;
  border-color: #ccc;
  cursor: not-allowed;
}

.button--small {
  font-size: 0.8rem;
}

.button--big {
  font-size: 1.5rem;
}
