.side-drawer {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 100;
  height: 100vh;
  width: 70%;
  max-width: 400px;
  background: white;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  padding: 2rem 1rem;
  overflow-y: auto;
}

.main-navigation__drawer-nav {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  padding: 1rem 0;
}

/* Mobile screens */
@media (max-width: 767px) {
  .side-drawer {
    width: 85%;
    padding: 1.5rem 0.75rem;
  }
}

/* Small mobile screens */
@media (max-width: 480px) {
  .side-drawer {
    width: 100%;
    padding: 1rem 0.5rem;
  }
}
