.modal__button {
  margin-top: 2rem;
  margin-bottom: 1rem;
  margin-left: 30rem;
}

.error-header {
  display: flex;
  align-items: center;
  gap: 1rem;
  color: #2d3436;
}

.error-header h2 {
  font-size: 1.8rem;
  font-weight: 600;
  margin: 0;
}

.error-icon {
  font-size: 2rem;
  color: #e74c3c;
  animation: pulse 1s ease-in-out infinite;
}

.error-content {
  padding: 1.5rem 0;
  color: #636e72;
  font-size: 1.1rem;
  line-height: 1.6;
}

.error-button {
  background: #00b894;
  color: white;
  padding: 0.8rem 2rem;
  border: none;
  border-radius: 50px;
  font-weight: 600;
  cursor: pointer;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.error-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 15px rgba(0, 184, 148, 0.3);
}

@keyframes pulse {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}
